.twtr-data-protection { position: fixed; width: 100%; bottom: 0; left: 0; z-index: 9999; background-color: #657786; padding-right: 24px; padding-left: 24px; }

.twtr-data-protection .is-hidden { display: none; }

.twtr-data-protection--eu, .twtr-data-protection--opt-in { padding-top: 32px; padding-bottom: 32px; }

@media (min-width: 1024px) { .twtr-data-protection__content { display: flex; width: 75%; max-width: 1020px; float: none; margin-left: auto; margin-right: auto; } }

@media (min-width: 1441px) { .twtr-data-protection__content { width: 55%; } }

.twtr-data-protection__copy p { color: #FFFFFF; font-size: 18px; }

@media (min-width: 1024px) { .twtr-data-protection__copy p:last-child { padding-bottom: 0; margin-bottom: 0; } }

.twtr-data-protection__copy a { color: #FFFFFF; text-decoration: underline; }

@media (min-width: 1024px) { .twtr-data-protection__choice { margin-left: 16px; margin-top: 8px; } }

.twtr-data-protection__choice-list { text-align: center; padding-left: 0; margin-top: 24px; }

@media (min-width: 1024px) { .twtr-data-protection__choice-list { text-align: left; margin-top: 0; } }

.twtr-data-protection__choice-list li { display: inline-block; list-style: none; margin-right: 32px; }

.twtr-data-protection__choice-list li:not(:last-child) { margin-bottom: 12px; }

.twtr-data-protection__choice-list li:last-child { margin-right: 0; }

@media (min-width: 1024px) { .twtr-data-protection__choice-list li { margin-right: 0; } }

.twtr-data-protection__button { min-width: 120px; background-color: transparent; border-radius: 18px; padding-top: 12px; padding-right: 24px; padding-bottom: 12px; padding-left: 24px; font-size: 14px; font-weight: 700; line-height: 1; text-align: center; border-color: transparent; outline: none !important; }

.twtr-data-protection__button:hover { cursor: pointer; text-decoration: underline; }

.twtr-data-protection__button--accept, .twtr-data-protection__button--ok { background-color: #FFFFFF; color: #1a76cc; /* contrast ratio 4.0+ */ }

.twtr-data-protection__button--decline { color: #FFFFFF; border-color: #FFFFFF; border-style: solid; border-width: 1px; }

/* If 2 notices are presented inside data protection widget style accordingly to account for collapsed margins and any other design flourishes */
.twtr-data-protection--eu ~ .twtr-data-protection--opt-in { margin-top: -32px; /* collapse padding-top */ padding-top: 32px; }

.twtr-data-protection--eu.is-hidden ~ .twtr-data-protection--opt-in { margin-top: 0; }

.twtr-data-protection--eu.is-active ~ .twtr-data-protection--opt-in .twtr-data-protection__content { border-top: solid 1px #FFFFFF; padding-top: 32px; }
